.MovieContainer{
    width: 100%;
    height: auto;

    padding-top: 90px;
}

.Movie{
    width: 100%;
    height: 100%;

}
.MoviePlayer{
    position: relative;
    width: 100%;
    min-height: 360px;
    padding-bottom: 50.25%;
}

iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.MovieInner{
    width: 100%;
    padding-inline: 5%;
    gap: 24px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.MoviesForYou{
    width: 350px;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 20px;
}



.MoviesForYouTitle {
    width: 100%;
    height: auto;
}
.MoviesForYouTitle h2{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;

    color: #FFFFFF;

}



.MovieForCard-Btn{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
}



.MovieForItemTitle{
    display: flex;
    z-index: 20;
    left: 0;
    bottom: 0;

    position: absolute;

    padding: 16px 12px;

}
.MovieForItemTitle h3{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;

    color: #FFFFFF;

}

.MovieForItem{
    width: auto;
    height: auto;

    cursor: pointer;

    user-select: none;
}
.MovieForCard-inner{
    width: 100%;
    height: auto;
}
.MovieForCard-container{
    width: auto;
    height: auto;

    border-radius: 3px;

    position: relative;
}
.MovieForCard-container img{
    width: 100%;
    height: 100%;
    border-radius: 3px;
    object-fit: cover;
}



.MovieList{
    width: 100%;
    height: auto;

    display: grid;
    grid-template-columns: auto auto; /* Duas colunas na horizontal */
    grid-template-rows: repeat(2, auto); /* Trs linhas */
    gap: 10px; /* Espao entre os elementos */
}
.MovieTitle{
    display: flex;
    flex-direction: column;
    gap: 8px;


}
.MovieTitle h2{
    font-family: 'Gilroy-Medium';
    font-style: normal;

    font-weight: 600;
    font-size: 32px;
    line-height: 60px;

    color: #FFFFFF;
}
.MovieTitle h3{
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 20px;
    color: #FFFFFF;
}
.MovieTitle p{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #737174;
}



.MovieDescriptionContainer{
    display: flex;
    flex-direction: column;
    gap: 18px;
}
.MovieDescriptionContainer h3{
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 20px;
    color: #FFFFFF;
}
.MovieDescriptionContainer p{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #737174;
}



.MovieContent{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;

    margin-top: 40px;

    gap: 40px;
}
.MoviePanel{
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}



.MovieBtns{
    display: flex;
    flex-direction: row;

    gap: 48px;


}

.MovieBtns span{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 16px;
}

.MovieBtns span svg{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.MovieBtns span:hover{
    color: #FFFFFF;
}

.DownloadIcon,
.WatchlistIcon,
.ShareIcon{
    cursor: pointer;
    font-family: 'Gilroy-Medium';
    line-height: 20px;
    color: #737174;
    font-size: 14px;
}


.DownloadIcon{
    color: #1884F7;
}
.DownloadIcon:hover svg path{
    fill: #FFFFFF;
}

.WatchlistIcon:hover svg rect{
    fill: #FFFFFF;
}
.ShareIcon:hover svg path{
    fill: #FFFFFF;
}




@media (max-width: 1180px) {
    .MoviesForYou {
        display: none;
      }
    
}

@media (max-width: 790px) {
    .MoviePanel{
        flex-direction: column;
        align-items: flex-start;

    }
}