.BackgroundComponent-Container {
    width: 100%;
    height: clamp(440px, 50vw, 720px);

    overflow: hidden;
}

.Bg-filter {
    user-select: none;
    z-index: 11;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(172.23deg, #000000b2 3%, rgba(0, 0, 0, 0.61) 30%, rgba(0, 0, 0, 0.322) 50%);
    mix-blend-mode: multiply;
}


.Img-container {
    width: 100%;
    height: 100%;
    z-index: -15;

}

.Img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.BGground{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
    z-index: -15;
}

@media screen and (min-width: 1280px) {
    .BackgroundComponent-Container .Menu-Slider {
        display: none;
    }
}