.PopularMoviesh1 {
    padding-inline: 5%;
    padding-bottom: 40px;

    display: flex;
    justify-content: space-between;
}

.PopularMoviesh1 h1 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.PopularContent{
    width: 100%;
    height: 385px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    position: relative;
}


.PopularInner{
    width: 100%;
    height: auto;

    display: flex;
    align-items: center;


}


.PopularList{
    z-index: 21;
    width: 100%;
    height: 385px;

    overflow-x: scroll;
    overflow-y: hidden;

    display: flex;
    flex-direction: row;
    
    gap: 16px;

    padding-inline: 5%;

    position: relative;

    scrollbar-width: none;
    scrollbar-color: transparent transparent;
}

.PopularList::-webkit-scrollbar {
    width: 0;
    height: 0;
  }




.PopularItem{
    cursor: pointer;


    width: 226px;
    height: 307px;

    display: flex;
    flex-direction: column;
    align-items: center;

}

.PopularItem:hover .PopularCard-inner{
    background-color: #FFFFFF;

    padding: 13px;

    width: 250px;
    height: 385px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;

}

.PopularItem:hover .PopularInfo-Container,
.PopularCard-Btn{
    display: flex;
}

.PopularItem:hover .PopularCard-Btn{
    display: flex;
}
.PopularItem:hover .downloadCard{
    display: flex;
}
.PopularItem:hover .PopularItemH1{
    display: flex;
}


.PopularCard-inner{

    width: 226px;
    height: 307px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;

    position: relative;

    transition: all 0.3s ease-in-out;
}




.PopularItem img{
    width: 100%;
    height: 100%;
    border-radius: 3px;
    object-fit: cover;

}


.PopularInfo-Container{
    display: none;
    flex-direction: column;
    width: 100%;
    height: 100%;

    justify-content: space-between;

    line-height: 24px;

    transition: all 0.3s ease-in-out;

    overflow: hidden;
}

.PopularInfo-Container h2{
    font-family: 'Gilroy-SemiBold';
    font-size: 16px;
    color: #0A070B;
}
.PopularInfo-Container h3{
    font-family: 'Gilroy-Medium';
    font-weight: 500;
    font-size: 14px;
    color: #4F4E50;
}


.PopularCard-container{
    width: 100%;
    height: 307px;


    position: relative;
    border-radius: 3px;


}
.downloadCard{
    position: absolute;
    z-index: 21;

    right: 15px;
    bottom: 10px;

    display: none;


}


.PopularCard-Btn{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: none;
    z-index: 20;
}



.bg-filter{
    opacity: 75%;
    
    -webkit-user-select: none;
    user-select: none;

    z-index: 10;

    width: 100%;
    height: 100%;
    position: absolute;

    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 35%, rgba(10,7,11,1) 100%);

    border-radius: 3px;
    
}

.PopularItemH1{
    z-index: 20;
    left: 0;
    bottom: 0;

    position: absolute;

    padding: 12px 16px;

    display: none;
}


.PopularItemH1 h1{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;

    color: #FFFFFF;

}

.PopularContent:hover #Popular-prevbtn {
    opacity: 100%;
}

.PopularContent:hover #Popular-nextbtn {
    opacity: 100%;
}

#Popular-prevbtn {

    opacity: 0%;

    display: flex;
    transition: all 0.3s;
    z-index: 22;
    left: 0;

    top: 20%;
    bottom: auto;

    align-self: center;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 180px;


    align-items: center;
    justify-content: center;

}

#Popular-nextbtn {
    opacity: 0%;
    z-index: 22;


    top: 20%;
    bottom: auto;

    right: 0;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 180px;

    display: flex;
    align-items: center;
    justify-content: center;
}

#Popular-nextbtn:hover {
    cursor: pointer;

}


#Popular-prevbtn:hover svg{
    transform: scale(1.3);
}

#Popular-nextbtn:hover svg{
    transform: scale(1.3);
}

#Popular-prevbtn:hover {
    cursor: pointer;
}


#Popular-nextbtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#Popular-prevbtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}






@media screen and (max-width: 768px) {

    .PopularItem{
        min-width: 150px;
        height: 210px;
    }
    .PopularCard-inner{
        width: 150px;
        height: 210px;
    }
    .PopularCard-container{
        width: 150px;
        height: 210px;
    }
    .PopularList {
        overflow-x: scroll;
        z-index: 20;
        height: 210px;
        gap: 16px;
    }

    



.PopularItemH1{
    position: absolute;


    left: 0;
    z-index: 20;
    bottom: 6px;
    text-align: left;
    padding: 8px;
}


.PopularItemH1 h1{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 12px;

    color: #FFFFFF;

}

.PopularMoviesh1 h1 {
    font-size: 20px;
    line-height: 32px;
}

.PopularMoviesh1 h3 {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
}









.PopularItem:hover .PopularCard-inner{
    background-color: none;

    padding: 0;

    width: 150px;
    height: 170px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;

}


.PopularCard-Btn{
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
}

.PopularItem:hover .downloadCard{
    display: none;
}
.PopularItem:hover .PopularInfo-Container{
    display: none;
}


#Popular-prevbtn{
    height: 130px;
}
#Popular-nextbtn{
    height: 130px;
}

.PopularContent{
    height: 210px;
}

}