.Menu-Slider {
    z-index: 23;
    position: absolute;
    padding-left: clamp(20px, 5vw, 70px);
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    overflow: hidden;
    
}

.Menu-Inner {
    overflow-y: scroll;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 24px;
    transition: 0s ease-in;
    padding-left: clamp(20px, 5vw, 70px);
}

.Menu-Inner li {
    display: flex;
    align-items: center;
    justify-content: left;
    list-style: none;


    gap: 8px;
}

.Menu-Inner li a {
    text-decoration: none;
    white-space: nowrap;
    z-index: 23;
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;
}

.Menu-Inner li svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Menu-Inner li {
    outline: none;
    cursor: pointer;
    user-select: none;
    z-index: 23;
}

.Menu-Inner li svg path {
    fill: #737174
}

.Menu-Inner li:active svg path {
    fill: #E8ECEF;
}

.Menu-Inner li:active a {
    color: #E8ECEF;

}


