@font-face {
    font-family: 'Gilroy-Bold';
    src: url(./Layout/Fonts/EM-Gilroy/Gilroy-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-Regular';
    src: url(./Layout/Fonts/EM-Gilroy/Gilroy-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-Medium';
    src: url(./Layout/Fonts/EM-Gilroy/Gilroy-Medium.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-SemiBold';
    src: url(./Layout/Fonts/EM-Gilroy/Gilroy-SemiBold.ttf) format('OpenType');
}

html{
    width: 100%;
    height: auto;
}

* {
    margin: 0;
    padding: 0;
    
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;

    scroll-behavior: smooth;
}

body {
    width: 100%;
    height: auto;
    background: #0A070B;

    scrollbar-width: thin;
    scrollbar-color: #F41B3B  #15161C;
}

html::-webkit-scrollbar {
    width: 12px;
    background-color: #15161C;
}

html::-webkit-scrollbar-thumb {
    background-color: #F41B3B;
    border-radius: 6px;
}

html::-webkit-scrollbar-thumb {
    background-color: #F41B3B; 
}


#root {
    --nav-height: 90px; 
}

footer{
    display: flex;
    flex-direction: column;
    align-items: center;

    background: #0A070B;
    padding-block: 24px;
    padding-inline: 5%;

    border: solid #4F4E50;
    border-width: 2px 0px 0px 0px;
    gap: 24px;
}


.navLogada{
    width: 100%;
    z-index: 22;
    top: 0;
    left: 0;
    position: fixed;
    background-color: #0A070B;
}

.App{
    width: 100%;
    height: 100%;
}

.App > .HomePage > .navLogada > header{
    padding-top: 200px;
}

.navNormal .nav-container-default{
    width: 100%;
    z-index: 23;
    top: 0;
    left: 0;
}


body > .navNormal{
    padding-top: 0;
}




