.LiveshowTitle {
    padding-inline: 5%;
    padding-bottom: 5%;
    display: flex;
    justify-content: space-between;
}

.LiveshowTitle h2 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.LiveShowContent {
    position: relative;
    width: 100%;
    height: auto;
}



.SeeAll {
    cursor: pointer;
    width: auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
}


.SeeAll h3 {
    cursor: inherit;
    font-family: 'Gilroy-regular';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}

.SeeAllSvg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.ShowItem {
    min-width: 360px;
    height: 100px;

    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 16px;

    position: relative;
}



.LiveshowImgContainer{
    display: flex;
    width: 160px;
    height: 100px;
    object-fit: cover;
    border-radius: 2px;
}

.LiveshowImgContainer img {
    width: 160px;
    height: 100px;
    object-fit: cover;
    border-radius: 2px;
}

.ShowListInner {
    width: 100%;
    height: auto;
    display: flex;

    position: relative;

    overflow: hidden;

}


.ShowList {
    z-index: 21;

    overflow-x: scroll;
    overflow-y: hidden;

    display: flex;
    flex-direction: row;

    width: 100%;
    height: auto;

    gap: 24px;

    padding-inline: 5%;

    scrollbar-width: none;
    scrollbar-color: transparent transparent;
}
.ShowList::-webkit-scrollbar {
    width: 0;
    height: 0;
  }


.LiveShowInfo {
    width: auto;
    height: 100%;

    display: flex;
    flex-direction: column;

    justify-content: space-between;
}

.LiveShowInfoTitle {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    gap: 6px;

}

.LiveShowInfoTitle h2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    -webkit-box-orient: vertical;

    text-overflow: ellipsis;

    cursor: pointer;
    font-family: 'Gilroy-medium';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;

    color: #FFFFFF;


}

.LiveShowInfoTitle p {
    font-family: 'Gilroy-regular';
    font-size: 14px;
    line-height: 20px;

    color: #807E81;
}



.LiveShowContent:hover #LiveShow-prevbtn {
    opacity: 100%;
}

.LiveShowContent:hover #LiveShow-nextbtn {
    opacity: 100%;
}

#LiveShow-prevbtn {

    opacity: 0%;

    display: flex;
    transition: all 0.3s;
    z-index: 22;
    left: 0;
    top: 0;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 100%;

    align-items: center;
    justify-content: center;
}

#LiveShow-nextbtn {
    opacity: 0%;
    z-index: 22;
    top: 0;
    right: 0;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

#LiveShow-nextbtn:hover {
    cursor: pointer;

}


#LiveShow-prevbtn:hover svg{
    transform: scale(1.3);
}

#LiveShow-nextbtn:hover svg{
    transform: scale(1.3);
}

#LiveShow-prevbtn:hover {
    cursor: pointer;
}


#LiveShow-nextbtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#LiveShow-prevbtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.hover-effect{
    width: 100%;
    height: 100%;
    position: absolute;

    top: 0;
    left: 0;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 14px;
    gap: 25px;

    opacity: 0;
    visibility: visible;
    transition: opacity 0.3s,  visibility 0.3s ease-in-out;

    z-index: 555;
}
.hover-effect h2{
    transition: all 0.5s ease-in-out;
}

.hover-effect h2:hover{
    color: #F9CC0D;
}


.Hover-Info{
    width: auto;
    height: 100%;
}
.hoverTitle{
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.btn-svg{
    width: 62px;
    height: 62px;
}






.hoverTitle h2{
    display: -webkit-box;
    -webkit-line-clamp: 1;
    overflow: hidden;
    -webkit-box-orient: vertical;

    text-overflow: ellipsis;

    cursor: pointer;
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;

    color: #FFFFFF;
}
.hoverTitle p{
    font-family: 'Gilroy-Medium';
    font-size: 14px;
    color: #737174;
}




.image-container{
    z-index: -5555;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
}
.image-container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-filter{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: #0b080cc0;
}

.LiveDownload{
    width: auto;
    height: auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;

    justify-content: flex-start;

    gap: 8px;
}
.LiveDownload p{

    text-align: bottom;
    color: #1884F7;
}











@media screen and (min-width: 768px){

    .ShowItem:hover .hover-effect{
        opacity: 100;
    }

    .ShowItem:hover .LiveshowImgContainer{
        opacity: 0;
    }

    .ShowItem:hover .LiveShowInfo{
        opacity: 0;
    }

}





@media screen and (max-width: 768px) {
    #LiveShow-nextbtn,
    #LiveShow-prevbtn{
        height: 50%;
 
    }



    .ShowListInner {
        height: auto;
    }

    .LiveShowContent {
        height: 180px;
    }

    .ShowList {
        overflow-x: scroll;
        z-index: 20;
        position: relative;
        height: 200px;
        gap: 16px;
    }

    .ShowItem {
        min-width: inherit;
        max-width: 200px;
        height: 180px;
        gap: 8px;
        flex-direction: column;
    }

    .LiveshowImgContainer {
        width: 160px;
        height: 100px;
    }

    .LiveshowImgContainer img {
        width: 160px;
        height: 99px;
    }

    .LiveShowInfo {
        width: 142px;
        height: auto;
        gap: 8px;
        padding-left: 0px;

    }

    .LiveShowInfo h2 {
        width: 141px;
        font-size: 14px;
        line-height: 18px;
    }



    .LiveShowContent {
        padding-left: 0;

        position: relative;
        width: auto;
        height: auto;
    }

    .LiveshowTitle h2 {
        font-size: 20px;
        line-height: 32px;
    }

    .LiveshowTitle h3 {
        font-weight: 600;
        font-size: 14px;
        line-height: 22px;
    }
}