.feature-container {
    z-index: 2;
    width: 100%;
    height: clamp(440px, 50vw, 800px);

    display: flex;
    flex-direction: row;
    align-items: center;

    position: relative;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);
}

.next-btn, .prev-btn {
    position: absolute;
    z-index: 10;
    cursor: pointer;
}

.next-btn:hover, .prev-btn:hover {
    filter: contrast(80%);
}

.next-btn:active, .prev-btn:active {
    filter: brightness(10%);
}

.prev-btn {
    left: 0;
}

.next-btn {
    transform: scaleX(-1);
    right: 0;
}

.prev-btn, .next-btn {
    top: 40%;
}



.slide-content {
    z-index: 2;
    width: 100%;
    height: clamp(440px, 50vw, 800px);

    overflow: hidden;
    position: relative;
    margin: auto;

}



.slider {
    overflow: auto;
    display: flex;
    flex-direction: row;
    position: relative;

    width: 500%;
    height: 100%;
}

.slide {
    border-radius: 3px;
    position: relative;

    width: 20%;
    height: 100%;

    display: flex;
    flex-direction: column;

    transition: .6s;
}

.slide img {
    user-select: none;
    z-index: -2;
    position: absolute;
    
    width: 100%;
    height: 100%;

    object-fit: cover;

    border-radius: 3px;
}







.feature-intro {

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    z-index: 2;
    height: 100%;
    max-width: 600px;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    gap: 42px;
    margin-bottom: clamp(20px, 10vw, 105px);


    color: #FFFFFF;

    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}

.feature-intro a {
    text-decoration: none;
    user-select: none;
    z-index: 2;
}

.feature-intro h3 {
    user-select: none;
    z-index: 2;
}
.feature-intro p {
    user-select: none;
    z-index: 2;
}


.feature-intro a h2{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(20px, 5vw, 38px);
    line-height: clamp(32px, 5vw, 48px);

    letter-spacing: -0.015em;
    text-transform: capitalize;

    margin-bottom: 1px;

    color: #ffff;
}

.feature-intro h3 {
    font-family: 'Gilroy-Medium';
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 3vw, 20px);
    line-height: clamp(22px, 3vw, 24px);

    margin-bottom: 8px;
}

.feature-intro p {
    font-family: 'Gilroy-SemiBold';
    font-size: 14px;
    line-height: 20px;
    color: #cecece;

    color-interpolation-filters: linearRGB;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);

    display: -webkit-box;
    -webkit-line-clamp: 4;
    overflow: hidden;
    -webkit-box-orient: vertical; 
}




.feature-btn {
    display: flex;
    flex-direction: row;
    text-align: center;

    gap: 20px;

}

.align-btn {
    gap: 16px;
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    flex: none;
    order: 1;
    flex-grow: 0;

    text-align: center;

    justify-content: center;
}

.align-btn p {
    user-select: none;

    width: 68px;
    height: 52px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding-bottom: 5px;
}

.align-btn svg {
    user-select: none;
}




.playnow-btn, .watchlist-btn {
    cursor: pointer;

    width: 191px;
    height: 66px;
    border-radius: 3px;

    padding: 0px 32px;
    gap: 16px;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;


}


.playnow-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(40px);
}

.playnow-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}

.playnow-btn:active {
    background: rgba(255, 255, 255, 0.37);

    transition: all 0s;
}




.watchlist-btn {
    display: flex;
    flex-direction: row;
    align-items: center;

    justify-content: center;

    border: 1px solid #FFFFFF;

    background-color: rgba(128, 126, 129, 0.01);
}

.watchlist-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}


.watchlist-btn:active {
    background-color: #807e813d;

    transition: all 0s;
}






@media(max-width: 768px) {

    .slide, .slide img{
        border-radius: inherit;
    }

    .feature-container {
        padding-left: 0;
        padding-right: 0;
    }

    .feature-intro h3 {
        display: none;
    }

    .feature-intro {
        width: 100%;
        height: 100%;
        max-width: 100%;
    }

    .feature-btn {
        padding: 0;
        flex-direction: column;
        padding-top: 13px;

        width: 100%;
    }



    .playnow-btn, .watchlist-btn {
        cursor: pointer;

        width: 100%;
        height: 66px;
        border-radius: 3px;

        padding: 0px 32px;
    }

    .prev-btn, .next-btn {
        display: none;
    }
}


















.filter {
    user-select: none;
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(259.96deg, rgba(0, 0, 0, 0.60) 1.2%, rgba(0, 0, 0, 0.25) 47.01%, rgba(0, 0, 0, 0.60) 100%);
    mix-blend-mode: multiply;
}



#slide1:checked~.s1 {
    margin-left: 0;
}

#slide2:checked~.s1 {
    margin-left: -20%;
}

#slide3:checked~.s1 {
    margin-left: -40%;
}

#slide4:checked~.s1 {
    margin-left: -60%;
}

#slide5:checked~.s1 {
    margin-left: -80%;
}

#slide6:checked~.s1 {
    margin-left: -100%;
}

.slider input {
    z-index: 50;
    display: none;
}