header{
    width: 100%;
    height: auto;
    
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
header > .Menu-Slider {
    z-index: 20;
    position: relative;
    padding-left: clamp(20px, 5vw, 70px);
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    overflow: hidden;
    
}

@media screen and (min-width: 1280px) {
        .Menu-Slider {
        display: none;
        z-index: 20;
        position: relative;
        padding-left: clamp(20px, 5vw, 70px);
        left: 0;
        top: 0;
        width: 100%;
        height: 60px;
        overflow: hidden;
        
    }
}
