


.SectionTitle {
    width: 100%;
    padding-inline: 5%;
    padding-bottom: 40px;

    display: flex;
    justify-content: space-between;
}

.SectionTitle h2 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.SliderContent{
    width: 100%;
    height: 300px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    position: relative;


}
.SliderContainer{
    padding-top: 20px;
}


.Slider-Inner{
    width: 100%;
    height: auto;

    display: flex;
    align-items: center;


    position: relative; 
}



.Slide-List{
    z-index: 21;
    width: 100%;
    height: 300px;

    overflow-x: scroll;
    overflow-y: hidden;

    display: flex;
    flex-direction: row;
    
    gap: 16px;


    position: relative;

    padding-inline: 5%;

    scrollbar-width: none;
    scrollbar-color: transparent transparent;
}
.Slide-List::-webkit-scrollbar {
    width: 0;
    height: 0;
  }





.Slide-Item{
    cursor: pointer;

    width: 400px;
    height: 240px;

    display: flex;
    flex-direction: column;
    align-items: center;

}

.Slide-Item:hover .Card-inner{
    background-color: #FFFFFF;
    padding: 13px;

    width: 400px;
    height: 300px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;
}

.Slide-Item:hover {

    height: 280px;
}



.Slide-Item:hover .Info-Container,
.Card-Btn{
    display: flex;
}

.Slide-Item:hover .Card-Btn{
    display: flex;
}
.Slide-Item:hover .downloadCard{
    display: flex;
}
.Slide-Item:hover .ItemTitle{
    display: flex;
}


.Card-inner{
    width: 400px;
    height: 240px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;

    position: relative;

    transition: all 0.3s ease-in-out;
}




.Slide-Item img{
    width: 100%;
    height: 100%;
    border-radius: 3px;
    object-fit: cover;

}


.Info-Container{
    display: none;
    flex-direction: column;
    width: 100%;
    height: 100%;

    justify-content: space-between;

    line-height: 24px;

    transition: all 0.3s ease-in-out;

    overflow: hidden;
}

.Info-Container h3{
    font-family: 'Gilroy-SemiBold';
    font-size: 16px;
    color: #0A070B;
}
.Info-Container p{
    font-family: 'Gilroy-Medium';
    font-weight: 500;
    font-size: 14px;
    color: #4F4E50;
}


.Card-container{
    width: 100%;
    height: 240px;


    position: relative;
    border-radius: 3px;
}

.downloadCard{
    position: absolute;
    z-index: 21;

    right: 15px;
    bottom: 10px;

    display: none;


}


.Card-Btn{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: none;

    z-index: 11;
}



.bg-filter{
    opacity: 75%;
    
    -webkit-user-select: none;
    user-select: none;

    z-index: 10;

    width: 100%;
    height: 100%;
    position: absolute;

    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 35%, rgba(10,7,11,1) 75%);

    border-radius: 3px;
    
}

.ItemTitle{
    z-index: 20;
    left: 0;
    bottom: 0;

    position: absolute;

    padding: 12px 16px;

    display: flex;
}


.ItemTitle h2{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;

    color: #FFFFFF;

}

.SliderContent:hover .PrevBtn {
    opacity: 100%;
}

.SliderContent:hover .NextBtn {
    opacity: 100%;
}

.PrevBtn {

    opacity: 0%;

    display: flex;
    transition: all 0.3s;
    z-index: 22;
    left: 0;

    top: 15%;
    bottom: auto;

    align-self: center;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 150px;


    align-items: center;
    justify-content: center;

}

.NextBtn {
    opacity: 0%;

    display: flex;
    transition: all 0.3s;
    z-index: 22;
    right: 0;

    top: 15%;
    bottom: auto;

    align-self: center;
    position: absolute;
    background-color: #00000079;
    width: 40px;
    height: 150px;


    align-items: center;
    justify-content: center;
}

.NextBtn:hover {
    cursor: pointer;

}


.PrevBtn:hover svg{
    transform: scale(1.3);
}

.NextBtn:hover svg{
    transform: scale(1.3);
}

.PrevBtn:hover {
    cursor: pointer;
}


.NextBtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.PrevBtn svg {
    width: 20px;
    height: 20px;
    transition: transform 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}






@media screen and (max-width: 768px) {
    .SliderContent{
        height: 166px;
    }

    .Slide-Item{
        min-width: 277px;
        height: 166px;
    }
    .Card-inner{
        width: 277px;
        height: 166px;
    }
    .Card-container{
        width: 277px;
        height: 166px;
    }
    .Slide-List {
        overflow-x: scroll;
        z-index: 20;
        height: 166px;
        gap: 16px;
    }





.ItemTitle{
    position: absolute;
    display: flex;

    left: 0;
    z-index: 20;
    bottom: 6px;
    text-align: left;
    padding: 8px;
}


.ItemTitle h2{
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 12px;

    color: #FFFFFF;

}

.SectionTitle h2 {
    font-size: 20px;
    line-height: 32px;
}

.SectionTitle h3 {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
}









.Slide-Item:hover .Card-inner{
    background-color: none;

    padding: 0;

    width: 150px;
    height: 170px;

    border-radius: 3px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    gap: 8px;

}


.Card-Btn{
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
}

.Slide-Item:hover .downloadCard{
    display: none;
}
.Slide-Item:hover .Info-Container{
    display: none;
}

.Slide-Item:hover .bg-filter{
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 35%, rgba(10,7,11,1) 75%);
}


.NextBtn {

    height: 100px;
    top: 20%;
}
.PrevBtn {

    height: 100px;
    top: 20%;
}

}