.Search{
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: row;
    gap: 10px;

    flex-wrap: wrap;

    background-color: rgb(192, 192, 192);

    list-style: none;

    padding: 50px 0px;
}

.Search li{
    width: 260px;
    height: 180px;
    border-radius: 8px;
}
.Search li img{
    width: 100%;
    height: 100%;
    border-radius: 8px;
}



